.callContainerdiv {
    width: 100%;
    height: 90vh;

    .serachdiv {
        width: 100%;
        height: 10%;
        display: flex;
        align-items: center;
        justify-content: center;

        .callSearchInput {
            width: 80%;
            height: 50px;
            border-radius: 10px;
            outline: none;
            border: none;
            font-size: 17pt;
            text-indent: 5px;
            background-color: rgb(226, 226, 226);
        }
    }

    .mySearchShow {
        width: 100%;
        height: 25%;
        overflow: scroll;
    }
    .mySearchShow::-webkit-scrollbar {
        display: none;
        -ms-overflow-style: none; 
        scrollbar-width: none; 
      }

    .tablediv {
        width: 100%;
        height: 53%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .inputandbackspacediv {
            display: flex;
            align-items: center;
            width: 80%;
            height:20%;

            .inputdiv {
                width: 90%;
                height: 100%;

                input {
                    width: 100%;
                    height: 80%;
                    font-size: 30pt;
                    border: none;
                    text-align: center;
                }
            }

            .backspacediv {
                width: 10%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                font-size: 25pt;
                color: rgb(159, 159, 159);
            }

        }

        table {
            width: 80%;
            height: 80%;
            tr {
                height: 25%;
                background-color: rgb(236, 236, 236);

                td {
                    text-align: center;
                    font-size: 28pt;
                }
            }
        }
    }

    .callButtonDiv {
        width: 100%;
        height: 12%;
        display: flex;
        align-items: center;
        justify-content: center;
        
        .callButtonInnerDiv

        {
            text-decoration: none;
            width:4rem;
            height: 4rem;
            border-radius: 50%;
            background-color: rgb(78, 233, 145);
            color: white;
            font-size: 30pt;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}
@media only screen and (max-width:500px){
    .callContainerdiv
    {
        height: 80vh;
    }
}